.main {
  padding-bottom: 60px;
  &__inner {
    background-image: url("../img/main-img.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 20px;
    padding: 30px 60px;
  }
  &__group {
    max-width: 800px;
    background: rgba(255, 255, 255, 0.7);
    backdrop-filter: blur(6px);
    border-radius: 20px;
    padding: 40px;
  }
  &__title {
    font-size: 48px;
    line-height: 130%;
    font-weight: 500;
  }
  &__subtitle {
    font-size: 30px;
    line-height: 130%;
    font-weight: 500;
    margin-bottom: 40px;
  }
  &__list {
    margin-bottom: 40px;
    li {
      font-size: 21px;
      line-height: 130%;
    }
  }
  &__btn {
    height: 58px;
    gap: 10px;
    padding: 5px 50px;
    .btn-text {
      font-size: 18px;
    }
  }
}
.info {
  padding: 60px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 15px;
  }
  &__desc {
    width: 330px;
    flex-shrink: 0;
    background: #212431;
    border-radius: 20px;
    padding: 30px;
    color: #fff;
  }
  &__images {
    width: calc(100% - 345px);
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 30px / 3));
    gap: 15px;
  }
  &__img {
    position: relative;
    display: block;
    padding-bottom: 140%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 20px;
    }
  }
  &__group {
    display: flex;
    align-items: center;
    gap: 8px;
    margin-bottom: 15px;
  }
  &__icon {
    width: 40px;
    flex-shrink: 0;
    display: flex;
    align-items: center;
    img {
      width: 100%;
    }
  }
  &__info {
    background: #fff;
    border-radius: 15px;
    padding: 5px 10px;
    color: #212431;
    font-size: 19px;
    line-height: 130%;
    font-weight: 500;
  }
  &__text {
    font-size: 26px;
    line-height: 130%;
    margin-bottom: 15px;
  }
  &__text2 {
    font-size: 21px;
    line-height: 130%;
    font-weight: 300;
  }
}
.roadmap {
  padding: 60px 0 180px;
  &__title {
    margin-bottom: 10px;
  }
  &__subtitle {
    margin-bottom: 60px;
  }
  &__items {
    position: relative;
    z-index: 3;
    display: grid;
    grid-template-columns: repeat(5, calc(100% / 5 - 120px / 5));
    gap: 30px;
    margin-bottom: 60px;
  }
  &__item {
    position: relative;
    &:before {
      content: '';
      position: absolute;
      top: 80px;
      left: 1px;
      width: calc(100% + 29px);
      height: 3px;
      background: #212431;
      pointer-events: none;
    }
    &:after {
      content: '';
      position: absolute;
      z-index: 2;
      top: 70px;
      left: 0;
      width: 20px;
      height: 20px;
      background: #fff;
      box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.3);
      border-radius: 50%;
      pointer-events: none;
    }
    &-icon {
      max-width: 50px;
      height: 50px;
      margin-bottom: 60px;
      opacity: 0.5;
      img {
        max-width: 100%;
      }
    }
    &-price {
      display: inline-block;
      background: #212431;
      border-radius: 7px;
      padding: 3px 10px;
      font-size: 12px;
      line-height: 130%;
      color: #fff;
      margin-bottom: 15px;
    }
    &-title {
      font-size: 21px;
      line-height: 130%;
      margin-bottom: 15px;
    }
    &-info {
      font-size: 21px;
      line-height: 130%;
      color: #007c03;
      margin-bottom: 15px;
      opacity: 0.75;
    }
    .orange {
      color: #db7000;
    }
    &-info2 {
      font-size: 18px;
      line-height: 130%;
      font-weight: 300;
      color: #000;
      opacity: 0.2;
    }
  }
  &__bottom {
    position: relative;
  }
  &__btn {
    position: relative;
    z-index: 2;
    height: 58px;
    gap: 10px;
    padding: 5px 50px;
    span {
      font-size: 18px;
    }
  }
  &__images {
    position: absolute;
    top: 50%;
    left: 350px;
    transform: translate(0, -50%);
  }
  &__img {
    transform: rotate(10deg);
    width: 200px;
    img {
      width: 100%;
    }
  }
  &__img2 {
    position: absolute;
    z-index: 2;
    top: 60px;
    left: 100px;
    transform: rotate(-10deg);
    width: 200px;
    img {
      width: 100%;
    }
  }
}
.excursion {
  padding: 60px 0;
  &__inner {
    background: #212431;
    border-radius: 15px;
    padding: 60px;
    color: #fff;
  }
  &__title {
    margin-bottom: 10px;
  }
  &__subtitle {
    font-size: 45px;
    margin-bottom: 60px;
    opacity: 0.55;
  }
  &__images {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 30px / 4));
    gap: 10px;
    margin-bottom: 30px;
  }
  &__img {
    position: relative;
    padding-bottom: 58%;
    &:nth-child(2) {
      grid-column: 2/4;
      grid-row: 1/3;
    }
    &:nth-child(3) {
      grid-column: 4/5;
      grid-row: 1/3;
    }
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 15px;
    }
  }
  &__btn {
    height: 58px;
    gap: 10px;
    padding: 5px 50px;
    margin: 0 auto;
    .btn-text {
      font-size: 18px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .info {
    &__desc {
      width: 300px;
    }
    &__images {
      width: calc(100% - 315px);
    }
    &__text {
      font-size: 24px;
    }
    &__text2 {
      font-size: 19px;
    }
  }
}

@media screen and (max-width: 992px) {
  .main {
    padding-bottom: 40px;
    &__inner {
      padding: 30px;
    }
    &__group {
      max-width: 600px;
      padding: 30px;
    }
    &__title {
      font-size: 36px;
    }
    &__subtitle {
      font-size: 24px;
      margin-bottom: 30px;
    }
    &__list {
      margin-bottom: 30px;
      li {
        font-size: 16px;
      }
    }
    &__btn {
      height: 42px;
      border-radius: 12px;
      padding: 2px 24px;
      .btn-text {
        font-size: 16px;
      }
    }
  }
  .info {
    padding: 40px 0;
    &__inner {
      flex-direction: column;
    }
    &__desc {
      width: 100%;
    }
    &__images {
      width: 100%;
    }
  }
  .roadmap {
    padding: 40px 0;
    &__inner {
      position: relative;
    }
    &__subtitle {
      margin-bottom: 30px;
    }
    &__items {
      grid-template-columns: repeat(2, calc(50% - 30px / 2));
      margin-bottom: 0;
    }
    &__item {
      &:before {
        left: -15px;
        width: calc(100% + 45px);
      }
      &:last-child {
        &:before {
          width: 34px;
        }
      }
      &-title {
        font-size: 19px;
      }
      &-info {
        font-size: 19px;
      }
      &-info2 {
        font-size: 16px;
      }
    }
    &__bottom {
      position: absolute;
      right: 0;
      bottom: 0;
      width: 50%;
    }
    &__btn {
      width: 100%;
      height: 42px;
      border-radius: 12px;
      padding: 2px 12px;
      .btn-text {
        font-size: 16px;
      }
    }
    &__images {
      top: unset;
      left: calc(50% - 75px);
      bottom: 120px;
      transform: translate(-50%, 0);
      display: flex;
      justify-content: center;
    }
    &__img {
      width: 150px;
    }
    &__img2 {
      top: 40px;
      width: 150px;
    }
  }
  .excursion {
    padding: 40px 0;
    &__inner {
      padding: 30px;
    }
    &__subtitle {
      font-size: 32px;
      margin-bottom: 30px;
    }
    &__btn {
      height: 42px;
      border-radius: 12px;
      padding: 2px 24px;
      .btn-text {
        font-size: 16px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .excursion {
    &__inner {
      position: relative;
    }
    &__images {
      grid-template-columns: repeat(2, calc(50% - 10px / 2));
      margin-bottom: 0;
    }
    &__img {
      &:nth-child(2) {
        grid-column: 1/3;
        grid-row: 3;
      }
      &:nth-child(3) {
        grid-column: 2/3;
        grid-row: 1/3;
      }
    }
    &__btn {
      position: absolute;
      left: 50%;
      bottom: 60px;
      transform: translate(-50%, 0);
      max-width: calc(100% - 60px);
      width: max-content;
    }
  }
}

@media screen and (max-width: 576px) {
  .main {
    padding-bottom: 30px;
    &__inner {
      padding: 25px 15px;
    }
    &__group {
      padding: 20px;
    }
    &__title {
      font-size: 28px;
    }
    &__subtitle {
      font-size: 18px;
      margin-bottom: 20px;
    }
    &__list {
      margin-bottom: 20px;
      li {
        font-size: 14px;
      }
    }
    &__btn {
      height: 38px;
      gap: 5px;
      padding: 2px 24px;
      .btn-text {
        font-size: 14px;
      }
    }
  }
  .info {
    padding: 30px 0;
    &__inner {
      gap: 10px;
      background: #212431;
      border-radius: 20px;
      padding: 25px 15px;
    }
    &__desc {
      background: transparent;
      padding: 0;
    }
    &__images {
      grid-template-columns: repeat(2, calc(50% - 10px));
      gap: 10px;
    }
    &__img:first-child {
      grid-column: 1/3;
    }
    &__icon {
      width: 30px;
    }
    &__info {
      font-size: 14px;
    }
    &__text {
      font-size: 18px;
    }
    &__text2 {
      font-size: 14px;
    }
  }
  .roadmap {
    padding: 30px 0;
    &__items {
      grid-template-columns: 100%;
      margin-bottom: 30px;
    }
    &__item {
      padding-left: 50px;
      &:before {
        left: 10px;
        width: 1px;
        height: calc(100% + 30px);
      }
      &:after {
        top: 65px;
      }
      &:last-child {
        &:before {
          display: none;
        }
      }
      &-icon {
        margin-bottom: 15px;
      }
    }
    &__bottom {
      position: static;
      width: 100%;
    }
    &__images {
      position: static;
      transform: unset;
      display: flex;
      justify-content: center;
      margin-bottom: 30px;
    }
    &__img {
      img {
        margin-left: 15px;
      }
    }
    &__img2 {
      position: static;
      img {
        margin-top: 45px;
        margin-left: -15px;
      }
    }
    &__btn {
      height: 38px;
      gap: 5px;
      padding: 2px 12px;
      .btn-text {
        font-size: 14px;
      }
    }
  }
  .excursion {
    padding: 30px 0;
    &__inner {
      padding: 25px 15px;
    }
    &__subtitle {
      font-size: 24px;
    }
    &__images {
      margin-bottom: 30px;
    }
    &__btn {
      position: relative;
      left: unset;
      bottom: unset;
      transform: unset;
      max-width: 100%;
      width: 100%;
      height: 38px;
      gap: 5px;
      padding: 2px 12px;
      .btn-text {
        font-size: 14px;
      }
    }
  }
}